import Vue from 'vue'
import VueI18n from 'vue-i18n'
Vue.use(VueI18n)

// 
const messages = {
  'en_US': require('./assets/i18n/en'),
  'sp_US': require('./assets/i18n/sp'),
  'ge_US': require('./assets/i18n/ge'),
  'it_US': require('./assets/i18n/it'),
  'ru_US': require('./assets/i18n/ru'),
  'po_US': require('./assets/i18n/po'),
  'ar_US': require('./assets/i18n/ar'),
  'ja_US': require('./assets/i18n/ja'),
  'ko_US': require('./assets/i18n/ko'),
  'fr_US': require('./assets/i18n/fr'),
}

let lang = localStorage.getItem('langs') || 'en_US';

//  export default
export default new VueI18n({
  locale : lang, // set locale 
  messages : messages // set locale messages
})